import React, { Component } from 'react';
import './App.css';
import Register from './Register';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            registerPage:[]
        }
    }

    componentWillMount(){
        var registerPage =[];
        registerPage.push(<Register appContext={this}/>);
        this.setState({registerPage:registerPage})
    }
    
    render() {
        return (
            <div>
                {this.state.registerPage}
            </div>
        );
    }
}

export default App;


