import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';
import axios from 'axios';
//import Login from './Login';

class Register extends Component {
    constructor(props) {
        super(props);
        this.state={
            first_name:'',
            last_name:'',
            email:'',
            password:'',
            mobile:''
        }
    }

    render() {
        return (
            <div>
                <MuiThemeProvider>
                    <div>
                        <AppBar title="Person Registration Details" />
                        <TextField hintText="Enter your First Name" floatingLabelText="First Name" onChange={(event, newValue) => this.setState({first_name:newValue})} /> <br/>
                        <TextField hintText="Enter your Last Name" floatingLabelText="Last Name" onChange={(event, newValue) => this.setState({last_name:newValue})} /> <br/>
                        <TextField hintText="Enter your Email Id" floatingLabelText="Email Id" onChange={(event, newValue) => this.setState({email:newValue})} /> <br/>
                        <TextField type="password" hintText="Enter your Password" floatingLabelText="Password" onChange={(event, newValue) => this.setState({password:newValue})} /> <br/>
                        <TextField hintText="Enter your Mobile Number" floatingLabelText="Mobile Number" onChange={(event, newValue) => this.setState({mobile:newValue})} /> <br/>
                        {/*<RaisedButton label="Submit" primary={true} style={style} />*/}
                        <RaisedButton label="Submit" primary={true} style={style} onClick={(event) => this.handleClick(event)}/>
                    </div>
                </MuiThemeProvider>
            </div>
        );
    }

    handleClick(event) {
        var apiBaseUrl = "http://localhost:8080/bootRestCURDApp";
        if(this.state.first_name.length>0 && this.state.last_name.length>0 && this.state.email.length>0 && this.state.password.length>0 && this.state.mobile.length>0) {
            var PersonDetails = {
                "fname" : this.state.first_name,
                "lname" : this.state.last_name,
                "email" : this.state.email,
                "password" : this.state.password,
                "mobile" : this.state.mobile
            }

            axios.post(apiBaseUrl+'/post', PersonDetails).then(function (response) {
                console.log(response);
                if(response.data.code === 200){
                    console.log("Person Registred Successfully");
                } else{
                    console.log("Something Went Wrong While Registration", response.data.code);
                }
            }).catch(function (error) {
                console.log(error);
            });
        } else{
            alert("Input field values are missing");
        }
    } 
}

const style = {
  margin: 15,
};

export default Register;


